@import './config';


.home {
    height:100vh;

    overflow:hidden;
    align-items: center;
    text-align: center;

    z-index:-1;

    background-color: $winter;
   
    
    display: flex;
    &__img{
        display: flex;
        flex-direction: column;
        margin: 0 auto;


    }
    

    svg{
        position: absolute;
        width: 300px;
        height: 300px;
        

        text{
            font-size: 5px;
            font-family: $open-sans;
            color: $primary;
            
        }
    }
    img{
        position: relative;
        @include circle;
        @include gray;
        
        margin: 50px;
        background: url(../assets/mine2.jpg);
        background-size: cover;
        

    }
   
    h2 {
        
        font-family: $Raleway;
        color:$primary;
        

        span {
         
            margin-left: 20vw;
            
            color: $secondary;
            font-size: 1.3rem;
            margin: 1rem;
            padding: 0 1rem;
            
           
            
        }
    }
    &__name {
        
        text-transform: uppercase;
        padding-bottom:1rem;
        margin:1rem;
        color:$purple;
        font-weight: 400;
        border-bottom: 1px solid $purple;
        letter-spacing: .5rem;
        
        
   
        span{
           
            font-size: 6vw;
          
            
        }
       
        
        &--last {
            color: $secondary;
            background :$purple;
   
            font-size: 8vw;
            
            
            
        }
    }
    &_h1{
        z-index: 1;
        text-align: left;
        
        
        p, button{
            margin:0 1rem;
            display: none;
            font-size: 1.1rem;
            letter-spacing: .4rem;

          
        }
        button{
            border: 1px solid $purple;
            margin: 2vh;
            display: inline-block;
            text-align: right;
            letter-spacing: .2rem;
        
            &:hover{
                background: $purple !important;
                cursor: pointer;
                
                a{
                    color: $secondary!important;
                    
                }
            }
        }
    }

    .wave {
        display:none;
    }
}